.top-nav {
  color: #fff;
  background-color: #333;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.top-nav a {
  color: #606060;
  margin-right: 10px;
  text-decoration: none;
}

.username {
  color: #000;
}

.profileSection {
  justify-content: center;
  height: auto;
  display: flex;
  box-shadow: 0 8px 24px #959da533;
}

.profileSection > div {
  background-color: #f9f9f9;
  border-radius: 8px;
  justify-content: space-evenly;
  align-items: center;
  width: 80%;
  margin-bottom: 20px;
  padding: 20px;
  display: flex;
}

.profileSection h2 {
  color: #333;
}

.myProfile {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.profilePicture {
  background-color: #ccc;
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

#profilePicturex2 {
  background-color: #ccc;
  border-radius: 50%;
  width: 200px;
}

.profileDetails {
  flex-direction: column;
  display: flex;
}

.myAccountUsername {
  font-weight: bold;
}

.deleteAccountButton {
  color: #fff;
  cursor: pointer;
  background-color: #598f59;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.deleteAccountButton:hover {
  background-color: #3f743f;
}

.latestComments {
  width: 80%;
}

.latestComments h3 {
  color: #333;
}

.latestComments p {
  margin-bottom: 10px;
}

.commentDiv {
  text-align: center;
  background-color: #f2f2f2;
  flex-direction: column;
  justify-content: flex-start;
}
